<template>
  <div>
    <h1>通知列表页</h1>
    <el-button type="primary" plain round class="btn" @click="tiao(0)" v-if="shenf!=2">新建</el-button>
    <div class="tab">
      <el-table :data="tableData" border style="width: 90%">
        <el-table-column prop="date" type="index" label="序号" width="180"></el-table-column>
        <el-table-column prop="tit" label="题目" width="180"></el-table-column>
        <el-table-column prop="time" label="注册时间">
          <template slot-scope="scope">
            <span>{{scope.row.time.substr(0,10)}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" round @click="kan(scope.row.id)">查看</el-button>
            <v-del :idx="scope.row.id" @del="dels($event)" v-if="shenf!=2"></v-del>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      tableData: [],
      xuan: "",
      shenf: ""
    };
  },
  watch: {},
  computed: {},
  methods: {
    tiao(id) {
      this.$router.push("/home/tongz/tongzxin?id=" + id);
    },
    kan(id) {
      this.$router.push("/home/tongz/tongzxin?id=" + id);
    },
    dels(id) {
      fn.ajax(this, api.deln, "get", { id: id }, d => {
        fn.ajax(this, api.findn, "get", {}, d => {
          this.tableData = d.data.data;
        });
      });
      // this.$http({
      //   url: api.deln,
      //   method: "get",
      //   params: {
      //     id: id
      //   }
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.$http({
      //     url: api.findn,
      //     method: "get"
      //   }).then(d => {
      //     if (d.data.code == -1) {
      //       fn.errCallback(this, d);
      //     }
      //     this.tableData = d.data.data;
      //   });
      // });
    }
  },
  mounted() {
    this.shenf = localStorage.getItem("shenf");
     fn.ajax(this, api.findn, "get",{}, d => {
       this.tableData = d.data.data;
      });
    // this.$http({
    //   url: api.findn
    // }).then(d => {
    //   console.log(d);
    //   if (d.data.code == -1) {
    //     fn.errCallback(this, d);
    //   }
    //   this.tableData = d.data.data;
    // });
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 30px;
  padding: 20px;
  margin-bottom: 20px;
}

.btn {
  margin-left: 20px;
  margin-bottom: 20px;
}

.tab {
  margin-left: 100px;
}
</style>



