<template>
  <div>
    <h1>添加/修改通知</h1>
    <div class="input">
      <span>题目</span>
      <el-input v-model="user.tit" placeholder="请输入题目" clearable :disabled="this.shenf==2"></el-input>
    </div>
    <div class="inputs">
      <span>内容</span>
      <el-input
        type="textarea"
        :autosize="{ minRows: 2, maxRows: 4}"
        placeholder="请输入内容"
        v-model="user.con"
        :disabled="this.shenf==2"
      ></el-input>
    </div>
    <div class="block">
      <span class="demonstration">时间</span>
      <el-date-picker v-model="user.time" type="date" placeholder="选择日期" :disabled="this.shenf==2"></el-date-picker>
    </div>
    <div class="button" v-if="this.xuan==0">
      <el-button type="primary" round @click="yan()">发布</el-button>
      <el-button type="primary" round @click="fan()">返回</el-button>
    </div>
    <div class="button" v-if="this.xuan!=0">
      <el-button type="primary" round @click="gai()" v-if="this.shenf!=2">修改</el-button>
      <el-button type="primary" round @click="fan()">返回</el-button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      xuan: "",
      textarea: "",
      user: {
        tit: "",
        con: "",
        time: ""
      },
      shenf: ""
    };
  },
  methods: {
    fan() {
      this.$router.push("/home/tongz");
    },
    gai() {
      delete this.user._id;
      fn.ajax(this, api.upn, "get", this.user, d => {
        this.$router.push("/home/tongz");
      });
      // this.$http({
      //   url: api.upn,
      //   method: "get",
      //   params:this.user
      // }).then(d => {
      //   if(d.data.code==-1){
      //     fn.errCallback(this,d)
      //   }
      //   this.$router.push("/home/tongz");
      // });
    },

    yan() {
      if (this.user.tit == "" || this.user.tit == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入主题",
          type: "error"
        });
      }
      if (this.user.con == "" || this.user.con == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，内容不能为空",
          type: "error"
        });
      }
      if (this.user.time == null || this.user.time == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请选择日期",
          type: "error"
        });
      }
      if (this.date < this.user.time) {
        this.$message({
          showClose: true,
          message: "错了哦，请选择正确的日期",
          type: "error"
        });
      }
      fn.ajax(this, api.addn, "get", this.user, d => {
        this.$router.push("/home/tongz");
      });
      // this.$http({
      //   url: api.addn,
      //   method: "get",
      //   params: this.user
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   if (d.data.code == 2) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "error"
      //     });
      //   } else if (d.data.code == 0) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "success"
      //     });
      //     this.$router.push("/home/tongz");
      //   }
      // });
    }
  },
  mounted() {
    this.shenf = localStorage.getItem("shenf");
    this.xuan = this.$route.query.id;
    if (this.xuan != 0) {
      fn.ajax(this, api.findn + "?id=" + this.xuan, "get",{}, d => {
      this.user = d.data.data[0];
      });
      // this.$http({
      //   url: api.findn + "?id=" + this.xuan
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.user = d.data.data[0];
      // });
    }
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 28px;
  padding: 20px;
  margin-bottom: 20px;
}

.input {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-input {
    float: left;
    width: 80%;
  }
}

.inputs {
  height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    line-height: 60px;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-textarea {
    float: left;
    height: 60px;
    width: 80%;

    .el-textarea__inner {
      height: 60px;
    }
  }
}

.block {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  .demonstration {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-date-editor {
    float: left;
    width: 80%;
  }
}

.button {
  text-align: center;
  margin: 20px;
}
</style>


