<template>
 <div>
 <router-link to='/home/guan' active-class='select' v-if="shenf==0">管理员管理</router-link>
 <router-link to='/home/user' active-class='select' v-if="shenf!=2">用户管理</router-link>
 <router-link to='/home/tongz' active-class='select'>通知</router-link>
 <router-link to='/home/sheb' active-class='select'>设备管理</router-link>
 </div>
</template>

<script type="text/ecmascript-6">
export default {
 components: {},
 props: {},
 data () {
 return {
     shenf:''
 };
 },
 watch: {},
 computed: {},
 methods: {},
 mounted () {
     this.shenf=localStorage.getItem('shenf')
 }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';
a{
    display: block;
    height: 60px;
    margin:0.1rem;
    font-size:30px;
    background: $bg1;
    line-height:60px;
    text-align: center;
    text-decoration :none;
    margin:20px 0;
    font-size: 15px;
    color:$bai;
    transition: all 0.5s
}
a:hover{
    background: $bg8;
    color:$ft;
    border-radius:10px;
}
.select{
    background: $bg7;
    color:$ft;
    border-radius:10px;
}
</style>



