<template>
  <div>
    <h1>用户列表页</h1>
    <el-button type="primary" plain round class="btn" @click="tiao(0)">新建</el-button>
    <div class="tab">
      <el-table :data="tableData" border type="index" style="width: 90%">
        <el-table-column
          fixed
          prop="tableData.length"
          type="index"
          label="序号"
          width="80"
          class="tab"
        ></el-table-column>
        <el-table-column prop="name" label="账号" width="120"></el-table-column>
        <el-table-column prop="tel" label="电话" width="150"></el-table-column>
        <el-table-column prop="email" label="邮箱" width="150"></el-table-column>
        <el-table-column prop="sex" label="性别" width="80"></el-table-column>
        <el-table-column prop="idx" label="身份证号" width="200"></el-table-column>
        <el-table-column fixed="right" label="操作" width="200">
          <template slot-scope="scope">
            <el-button type="primary" round @click="kan(scope.row.id)">查看</el-button>
            <v-del :idx="scope.row.id" @del="dels($event)"></v-del>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      tableData: [],
    };
  },
  watch: {},
  computed: {},
  methods: {
    tiao(id) {
      this.$router.push("/home/user/userxin?id=" + id);
    },
    kan(id) {
      this.$router.push(
        "/home/user/userxin?id="+id
      );
    },
    dels(id) {
      fn.ajax(this, api.delu, "get",{id: id}, d => {
        fn.ajax(this,api.findu, "get",{},d=>{
          this.tableData = d.data.data;
        })
      });
      // this.$http({
      //   url: api.delu,
      //   method: "get",
      //   params: {
      //     id: id
      //   }
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.$http({
      //     url: api.findu,
      //     method: "get"
      //   }).then(d => {
      //     if (d.data.code == -1) {
      //       fn.errCallback(this, d);
      //     }
      //     this.tableData = d.data.data;
      //   });
      // });
    }
  },
  mounted() {
    fn.ajax(this, api.findu, "get",{}, d => {
       this.tableData = d.data.data;
      });
    // this.$http({
    //   url: api.findu
    // }).then(d => {
    //   if (d.data.code == -1) {
    //     fn.errCallback(this, d);
    //   }
    //   this.tableData = d.data.data;
    // });
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 30px;
  padding: 20px;
  margin-bottom: 20px;
}

.btn {
  margin-left: 20px;
  margin-bottom: 20px;
}

.tab {
  margin-left: 100px;
}
</style>



