<template>
  <div class="bg">
    <div class="kuang">
      <h1>登&nbsp;&nbsp;&nbsp;录</h1>
      <el-input
        placeholder="请输入账号"
        v-model="user.name"
        clearable
        prefix-icon="el-icon-s-custom"
        class="inp"
      ></el-input>

      <el-input
        placeholder="请输入密码"
        v-model="user.pass"
        show-password
        class="inp"
        prefix-icon="el-icon-sunny"
      ></el-input>

      <el-select v-model="user.type" placeholder="请选择您的身份" class="inp" @change="currentSel">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <br>
      <el-button type="primary" class="deng" @click="deng()" :plain="true">登录</el-button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      user: {
        name: "",
        pass: "",
        type: ""
      },
      options: [
        {
          value: "0",
          label: "超级管理员"
        },
        {
          value: "1",
          label: "管理员"
        },
        {
          value: "2",
          label: "普通用户"
        }
      ],
      shen: ""
    };
  },
  watch: {},
  computed: {},
  methods: {
    currentSel(selVal) {
      this.shen = selVal;
    },
    deng() {
      if (this.user.name == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入账号",
          type: "error"
        });
      } else if (this.user.pass == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入密码",
          type: "error"
        });
      } else if (this.user.type === "") {
        this.$message({
          showClose: true,
          message: "错了哦，请选择您的身份",
          type: "error"
        });
      } else {
        this.$http({
          url: api.login,
          method: "post",
          data: this.user
        }).then(d => {
          console.log(d);
          if (d.data.code == -1) {
            fn.errCallback(this, d);
          }
          if (d.data.code == 2) {
            this.$message({
              showClose: true,
              message: d.data.info,
              type: "error"
            });
          } else if (d.data.code == 3) {
            this.$message({
              showClose: true,
              message: d.data.info,
              type: "error"
            });
          } else if (d.data.code == 0) {
            localStorage.setItem("shenf", this.shen);
            localStorage.setItem("ming", this.user.name);
            this.$router.push("/home");
            this.$message({
              showClose: true,
              message: "登陆成功，欢迎使用皮皮虾系统",
              type: "success"
            });
          }
        });
      }
    }
  },
  mounted() {}
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

.bg {
  overflow: hidden;
  height: 100vh;
  width: 100%;
  background: url('../../assets/111.jpeg') no-repeat center center;
  background-size: 100% 100%;
  position: relative;
}

.kuang {
  height: 300px;
  width: 400px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
}

.kuang h1 {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  color: $bai;
  margin-bottom: 20px;
  font-family: 'PingFang SC';
}

.kuang input {
  display: block;
  height: 40px;
  margin-bottom: 20px;
}

.kuang .inp {
  margin-bottom: 20px;
}
</style>



