<template>
  <div>
    <h1>添加/修改用户</h1>
    <div>
      <div class="input">
        <span>账号</span>
        <el-input v-model="user.name" placeholder="请输入账号" clearable :disabled="this.xuan!=0"></el-input>
      </div>
      <div class="input">
        <span>密码</span>
        <el-input
          placeholder="请输入密码"
          v-model="user.pass"
          show-password
          clearable
          :disabled="this.xuan!=0"
        ></el-input>
      </div>
      <div class="input" v-if="this.xuan==0">
        <span>确认密码</span>
        <el-input placeholder="请确认密码" v-model="pwds" show-password clearable></el-input>
      </div>
      <div class="input">
        <span>电话</span>
        <el-input v-model="user.tel" placeholder="请输入电话" clearable></el-input>
      </div>
      <div class="input">
        <span>邮箱</span>
        <el-input v-model="user.email" placeholder="请输入邮箱" clearable></el-input>
      </div>
      <div class="xuan">
        <span>性别</span>
        <div class="rd">
          <el-radio v-model="user.sex" label="男" :disabled="this.xuan!=0">男</el-radio>
          <el-radio v-model="user.sex" label="女" :disabled="this.xuan!=0">女</el-radio>
        </div>
      </div>
      <div class="input">
        <span>身份证号</span>
        <el-input v-model="user.idx" placeholder="请输入身份证号" clearable :disabled="this.xuan!=0"></el-input>
      </div>
      <div class="input">
        <span>备注</span>
        <el-input v-model="user.des" placeholder="请输入备注" clearable></el-input>
      </div>
      <div class="block">
        <span class="demonstration">生日</span>
        <el-date-picker v-model="user.time" type="date" placeholder="选择日期"></el-date-picker>
      </div>
      <div class="button" v-if="this.xuan==0">
        <el-button type="primary" round @click="yan()">发布</el-button>
        <el-button type="primary" round @click="fan()">返回</el-button>
      </div>
      <div class="button" v-if="this.xuan!=0">
        <el-button type="primary" round @click="gai()">修改</el-button>
        <el-button type="primary" round @click="fan()">返回</el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";
import ze from "../../common/js/ze";

export default {
  components: {},
  props: {},
  data() {
    return {
      xuan: "",
      user: {},
      pwds: "",
      date: new Date()
    };
  },
  methods: {
    fan() {
      this.$router.push("/home/user");
    },
    gai() {
      delete this.user._id;
      fn.ajax(this, api.upu, "get", this.user, d => {
        this.$router.push("/home/user");
      });
      // this.$http({
      //   url: api.upu,
      //   method: "get",
      //   params: this.user
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.$router.push("/home/user");
      // });
    },

    yan() {
      if (this.user.name == "" || this.user.name == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入账号",
          type: "error"
        });
        return;
      }
      if (this.user.pass == "" || this.user.pass == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入密码",
          type: "error"
        });
        return;
      }
      if (this.pwds != this.user.pass) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入与上面相同密码",
          type: "error"
        });
        return;
      }
      if (this.user.tel == "" || ze.tel(this.user.tel) == false) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入正确的电话号码",
          type: "error"
        });
        return;
      }
      if (this.user.email == "" || ze.email(this.user.email) == false) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入正确的电子邮箱",
          type: "error"
        });
        return;
      }
      if (this.user.sex == "" || this.user.sex == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请选择性别",
          type: "error"
        });
        return;
      }
      if (this.user.idx == "" || ze.idx(this.user.idx) == false) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入正确的身份证号码",
          type: "error"
        });
        return;
      }
      if (this.user.des == "" || this.user.des == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入备注",
          type: "error"
        });
        return;
      }
      if (this.user.time == null || this.user.time == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请选择日期",
          type: "error"
        });
        return;
      }
      if (this.date < this.user.time) {
        this.$message({
          showClose: true,
          message: "错了哦，请选择正确的日期",
          type: "error"
        });
        return;
      }
      fn.ajax(this, api.addu, "get", this.user, d => {
        this.$router.push("/home/user");
      });
      // this.$http({
      //   url: api.addu,
      //   method: "get",
      //   params: this.user
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   if (d.data.code == 2) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "error"
      //     });
      //   } else if (d.data.code == 0) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "success"
      //     });
      //     this.$router.push("/home/user");
      //   }
      // });
    }
  },
  mounted() {
    this.xuan = this.$route.query.id;
    if (this.xuan != 0) {
       fn.ajax(this, api.findu + "?id=" + this.xuan, "get",{}, d => {
      this.user = d.data.data[0];
      });
      // this.$http({
      //   url: api.findu + "?id=" + this.xuan
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.user = d.data.data[0];
      // });
    }
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 28px;
  padding: 20px;
  margin-bottom: 20px;
}

.input {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-input {
    float: left;
    width: 80%;
  }
}

.block {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  .demonstration {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-date-editor {
    float: left;
    width: 80%;
  }
}

.button {
  text-align: center;
  margin: 20px;
}

.xuan {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .rd {
    float: left;
    width: 80%;
  }
}
</style>


