<template>
  <div>
    <h1>添加/修改设备</h1>
    <div class="input">
      <span>ip</span>
      <el-input v-model="user.ip" clearable :disabled="this.shenf==2"></el-input>
    </div>
    <div class="input">
      <span>机房</span>
      <el-input v-model="user.door" clearable :disabled="this.shenf==2"></el-input>
    </div>
    <div class="input" v-if="this.xuan==0">
      <span>编号</span>
      <el-input v-model="user.num" clearable :disabled="this.shenf==2"></el-input>
    </div>
    <div class="input">
      <span>宽带</span>
      <el-input v-model="user.width" clearable :disabled="this.shenf==2"></el-input>
    </div>
    <div class="xuan">
      <span>状态</span>
      <div class="rd">
        <el-radio v-model="user.status" label="空闲" :disabled="this.shenf==2">空闲</el-radio>
        <el-radio v-model="user.status" label="已出售" :disabled="this.shenf==2">已出售</el-radio>
      </div>
    </div>
    <div class="button" v-if="this.xuan==0">
      <el-button type="primary" round @click="yan()">注册</el-button>
      <el-button type="primary" round @click="fan()">返回</el-button>
    </div>
    <div class="button" v-if="this.xuan!=0">
      <el-button type="primary" round @click="gai()" v-if="shenf!=2">修改</el-button>
      <el-button type="primary" round @click="fan()">返回</el-button>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      xuan: "",
      user: {
        ip: "",
        door: "",
        num: "",
        width: "",
        status: ""
      },
      shenf: ""
    };
  },
  methods: {
    fan() {
      this.$router.push("/home/sheb");
    },
    gai() {
      delete this.user._id;
      fn.ajax(this, api.upd, "get", this.user, d => {
        this.$router.push("/home/sheb");
      });
      // this.$http({
      //   url: api.upd,
      //   method: "get",
      //   params: this.user
      // }).then(d => {
      //   if(d.data.code==-1){
      //     fn.errCallback(this,d)
      //   }
      //   this.$router.push("/home/sheb");
      // });
    },

    yan() {
      if (this.user.ip == "" || this.user.ip == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入ip",
          type: "error"
        });
      }
      if (this.user.door == "" || this.user.door == undefined) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入机房",
          type: "error"
        });
      }
      if (this.user.num == null || this.user.num == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入编号",
          type: "error"
        });
      }
      if (this.user.width == null || this.user.width == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入宽带",
          type: "error"
        });
      }

      if (this.user.status == null || this.user.status == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请选择",
          type: "error"
        });
      }
      fn.ajax(this, api.addd, "get", this.user, d => {
        this.$router.push("/home/sheb");
      });
      // this.$http({
      //   url: api.addd,
      //   method: "get",
      //   params: this.user
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   if (d.data.code == 2) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "error"
      //     });
      //   } else if (d.data.code == 0) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "success"
      //     });
      //     this.$router.push("/home/sheb");
      //   }
      // });
    }
  },
  mounted() {
    this.shenf = localStorage.getItem("shenf");
    this.xuan = this.$route.query.id;
    if (this.xuan != 0) {
      fn.ajax(this, api.findd + "?id=" + this.xuan, "get",{}, d => {
      this.user = d.data.data[0];
      });
      // this.$http({
      //   url: api.findd + "?id=" + this.xuan,
      //   method: "get"
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.user = d.data.data[0];
      // });
    }
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 28px;
  padding: 20px;
  margin-bottom: 20px;
}

.input {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    width: 10%;
    text-align: right;
    margin-right: 5%;
  }

  .el-input {
    float: left;
    width: 85%;
  }
}

.block {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  .demonstration {
    float: left;
    width: 10%;
    text-align: right;
    margin-right: 5%;
  }

  .el-date-editor {
    float: left;
    width: 85%;
  }
}

.button {
  text-align: center;
  margin: 20px;
}

.xuan {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    width: 10%;
    text-align: right;
    margin-right: 5%;
  }

  .rd {
    float: left;
    width: 85%;
  }
}
</style>


