function tel (tels){
    var tel=/^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/ /*电话*/;
    return tel.test(tels)
}
function email(emails) {
    var email=/^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/  /*email*/
    return email.test(emails)
}
function idx(idxs) {
    var idx=/^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/ /*身份证*/
    return idx.test(idxs)
}
export default{
    tel,
    email,
    idx
}


