<template>
  <div>
    <h1>添加/修改管理员</h1>
    <div>
      <div class="input">
        <span>账号</span>
        <el-input v-model="user.name" clearable :disabled="this.xuan!=0"></el-input>
      </div>
      <div class="input">
        <span>密码</span>
        <el-input v-model="user.pass" show-password clearable :disabled="this.xuan!=0"></el-input>
      </div>
      <div class="input" v-if="this.xuan==0">
        <span>确认密码</span>
        <el-input v-model="pwd" show-password clearable></el-input>
      </div>
      <div class="input">
        <span>属性</span>
        <el-input v-model="user.prop" clearable></el-input>
      </div>
      <div class="block">
        <span class="demonstration">注册时间</span>
        <el-date-picker v-model="user.time" type="date"></el-date-picker>
      </div>
      <div class="button" v-if="this.xuan==0">
        <el-button type="primary" round @click="yan()">注册</el-button>
        <el-button type="primary" round @click="fan()">返回</el-button>
      </div>
      <div class="button" v-if="this.xuan!=0">
        <el-button type="primary" round @click="gai()">修改</el-button>
        <el-button type="primary" round @click="fan()">返回</el-button>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      user: {
        name: "",
        pass: "",
        prop: "",
        time: ""
      },
      pwd: "",
      date: new Date(),
      xuan: ""
    };
  },
  watch: {},
  computed: {},
  methods: {
    fan() {
      this.$router.push("/home/guan");
    },
    gai() {
      delete this.user._id;
       fn.ajax(this,api.upg, "get",this.user, d => {
        this.$router.push("/home/guan");
      });
      // this.$http({
      //   url: api.upg,
      //   method: "get",
      //   params:this.user
      // }).then(d => {
      //   console.log(d);
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.$router.push("/home/guan");
      // });
    },
    yan() {
      if (this.user.name == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入账号",
          type: "error"
        });
        return;
      }
      if (this.user.pass == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入密码",
          type: "error"
        });
        return;
      }
      if (this.pwd == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请确认密码",
          type: "error"
        });
        return;
      }
      if (this.pwd != this.user.pass) {
        this.$message({
          showClose: true,
          message: "错了哦，请输入与上面相同密码",
          type: "error"
        });
        return;
      }
      if (this.user.prop == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请输入属性",
          type: "error"
        });
        return;
      }
      if (this.user.time == null || this.user.time == "") {
        this.$message({
          showClose: true,
          message: "错了哦，请选择日期",
          type: "error"
        });
        return;
      }
      if (this.date < this.user.time) {
        this.$message({
          showClose: true,
          message: "错了哦，请选择正确的日期",
          type: "error"
        });
        return;
      }
       fn.ajax(this, api.addg, "get",this.user, d => {
          this.$router.push("/home/guan");
      });
      // this.$http({
      //   url: api.addg,
      //   method: "get",
      //   params: this.user
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   if (d.data.code == 2) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "error"
      //     });
      //   } else if (d.data.code == 0) {
      //     this.$message({
      //       showClose: true,
      //       message: d.data.info,
      //       type: "success"
      //     });
      //     this.$router.push("/home/guan");
      //   }
      // });
    }
  },
  mounted() {
    this.xuan = this.$route.query.id;
    if (this.xuan != 0) {
       fn.ajax(this, api.findg + "?id=" + this.xuan, "get",{}, d => {
      this.user = d.data.data[0];
      });
      // this.$http({
      //   url: api.findg + "?id=" + this.xuan,
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.user = d.data.data[0];
      // });
    }
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 28px;
  padding: 20px;
  margin-bottom: 20px;
}

.input {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  span {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-input {
    float: left;
    width: 80%;
  }
}

.block {
  height: 60px;
  line-height: 60px;
  overflow: hidden;
  margin-right: 50px;

  .demonstration {
    float: left;
    width: 15%;
    text-align: right;
    margin-right: 5%;
  }

  .el-date-editor {
    float: left;
    width: 80%;
  }
}

.button {
  text-align: center;
  margin: 20px;
}
</style>




