<template>
 <div class="box">
 <heads class="heads"></heads>
 <vmain class="main"></vmain>
 </div>
</template>

<script type="text/ecmascript-6">
import heads from '../view/heads'
import vmain from '../view/vmain'
export default {
 components: {
     heads,
     vmain,
 },
 props: {},
 data () {
 return {
 };
 },
 watch: {},
 computed: {},
 methods: {},
 mounted () {}
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';
.box{
    width: 100vw;
    height:100vh;
    display: flex;
    flex-direction: column;
}
.heads {
  position fixed
  top 0
  left 0
  background-color:$bg1;
  color: #333;
  line-height: 60px;
  height: 60px;
  width: 100%;
  overflow: hidden;
  z-index 20
}
.main{
flex: 1;
background-color: $bg2;
display: flex;
flex-direction: row;
padding-left 280px;
padding-top 60px;
box-sizing border-box
}
</style>



