<template>
  <div>
    <el-dropdown class="pp">
      <i class="el-icon-setting" style="margin-right: 15px"></i>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item @click.native="exit()">退出登录</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <span class="pp">{{ming}}</span>
    <span class="img">
      <img src="../../assets/22.jpg" alt>
    </span>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
export default {
  components: {},
  props: {},
  data() {
    return {
      shenf:'',
      ming:''
    };
  },
  watch: {},
  computed: {},
  methods: {
    exit() {
      this.$http({
        url: api.exit
      }).then(d => {
        if(d.data.code==-1){
          fn.errCallback(this,d)
        }
        this.$router.replace('/login')
      });
    }
  },
  mounted() {
    this.shenf=localStorage.getItem('shenf')
    this.ming=localStorage.getItem('ming')
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

.pp {
  float: right;
  margin: 0 10px;
}

.img {
  width: 40px;
  height: 40px;
  float: right;
  margin: 10px;
  border-radius: 50%;

  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}
</style>



