<template>
  <div>
    <h1>管理员列表页</h1>
    <el-button type="primary" plain round class="btn" @click="tiao(0)">新建</el-button>
    <div class="tab">
      <el-table :data="tableData" style="width: 90%">
        <el-table-column prop="tableData.length" type="index" label="序号" width="80"></el-table-column>
        <el-table-column prop="name" label="账号" width="120"></el-table-column>
        <el-table-column prop="prop" label="属性" width="120"></el-table-column>
        <el-table-column prop="time" label="注册时间" width="200">
          <template slot-scope="scope">
            <span>{{scope.row.time.substr(0,10)}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="address" label="操作">
          <template slot-scope="scope">
            <el-button type="primary" round @click="kan(scope.row.id)">查看</el-button>
            <v-del :idx="scope.row.id" @del="dels($event)"></v-del>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import api from "../../common/js/api";
import fn from "../../common/js/fn";

export default {
  components: {},
  props: {},
  data() {
    return {
      tableData: [],
      xuan: "",
      name: ""
    };
  },
  watch: {},
  computed: {},
  methods: {
    tiao(id) {
      this.$router.push("/home/guan/guanxin?id=" + id);
    },
    kan(id) {
      this.$router.push("/home/guan/guanxin?id=" + id);
    },
    dels(id) {
      fn.ajax(this, api.delg, "get",{id: id}, d => {
        fn.ajax(this,api.findg, "get",{},d=>{
          this.tableData = d.data.data;
        })
      });
      // this.$http({
      //   url: api.delg,
      //   method: "get",
      //   params: {
      //     id: id
      //   }
      // }).then(d => {
      //   if (d.data.code == -1) {
      //     fn.errCallback(this, d);
      //   }
      //   this.$http({
      //     url: api.findg,
      //     method: "get"
      //   }).then(d => {
      //     if (d.data.code == -1) {
      //       fn.errCallback(this, d);
      //     }
      //     this.tableData = d.data.data;
      //   });
      // });
    }
  },
  mounted() {
    // this.$http({
    //   url: api.findg,
    //   method: "get"
    // }).then(d => {
    //   if (d.data.code == -1) {
    //     fn.errCallback(this, d);
    //   }
    //   this.tableData = d.data.data;
    // });
     fn.ajax(this, api.findg, "get",{}, d => {
       this.tableData = d.data.data;
      });
  }
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';

h1 {
  height: 40px;
  line-height: 40px;
  font-size: 28px;
  padding: 20px;
  margin-bottom: 20px;
}

.btn {
  margin-left: 20px;
  margin-bottom: 20px;
}

.tab {
  margin-left: 100px;
}
</style>



