import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import login from '../components/page/login'
import welcome from '../components/page/welcome'
import home from '../components/page/home'
import guan from '../components/page/guan'
import guans from '../components/page/guans'
import user from '../components/page/user'
import users from '../components/page/users'
import tongz from '../components/page/tongz'
import tongzs from '../components/page/tongzs'
import sheb from '../components/page/sheb'
import shebs from '../components/page/shebs'
import guanxin from '../components/page/guanxin'
import userxin from '../components/page/userxin'
import shebxin from '../components/page/shebxin'
import tongzxin from '../components/page/tongzxin'
export default new Router({
  routes: [
    {
      path: '/login',
      component: login
    },
    {
      path: '/home',
      component: home,
      children: [
        {
         path:'',
         component:welcome,
        },
        {
          path: 'guan',
          component: guan,
          children: [
            {
              path: '',
              component: guans
            },
            {
              path: 'guanxin',
              component: guanxin
            }
          ]
        },
        {
          path: 'user',
          component: user,
          children: [
            {
              path: '',
              component: users
            },
            {
              path: 'userxin',
              component: userxin
            }
          ]
        },
        {
          path: 'tongz',
          component: tongz,
          children: [
            {
              path: '',
              component: tongzs
            },
            {
              path: 'tongzxin',
              component: tongzxin
            }
          ]
        },
        {
          path: 'sheb',
          component: sheb,
          children: [
            {
              path: '',
              component: shebs
            },
            {
              path: 'shebxin',
              component: shebxin
            }
          ]
        }
      ]
    },
    {
      path: '*',
      redirect: '/login'
    }
  ]
})



