import axios from 'axios'

function errCallback(obj, d) {
    obj.$message({
        message: d.data.info,
        type: 'warning'
    });
    obj.$router.replace('/login')
    return;
}

function ajax(obj, url, method, params, callback) {
    var paramsn = 'params';
    if (method.toUpperCase() == 'POST') {
        params = qs.stringify(params)
        paramsn = 'data'
    }
    axios({
        url: url,
        method: method,
        [paramsn]: params
    }).then(d => {
        if (d.data.code == -1) {
            errCallback(obj, d)
        }
        if (d.data.isok) {
            callback(d)
        } else {
            obj.$message({
                message: d.data.info,
                type: 'warning'
            });
        }
    })
}


export default {
    errCallback,
    ajax
}


