<template>
  <div>
    <lf class="lf"></lf>
    <rts class="rts"></rts>
  </div>
</template>

<script type="text/ecmascript-6">
import lf from "../view/lf";
import rts from "../view/rt";
export default {
  components: {
    lf,
    rts
  },
  props: {},
  data() {
    return {};
  },
  watch: {},
  computed: {},
  methods: {},
  mounted() {}
};
</script>
<style lang='stylus' scoped>
@import '../../common/stylus/index.styl';
.lf {
  width: 280px;
  height: 100%;
  background: $bg6;
  padding:0 10px;
  box-sizing: border-box;
  position fixed 
  left 0
  top 60px;
  z-index 20
}
.rts{
    flex:1;
    box-sizing: border-box;
    width 100%
}
</style>



